

.title.app {
    font-size: 75px;
}

.desc {
    color: dimgray;
}

.desc.large {
    color: dimgray;
    font-size: 25px;
}

.anchor {
    background-color: black;
    border-radius: 15px;
    padding: 15px;
    color: white;
    text-decoration: none;
    transition: 0.3s ease-out;
}

.anchor:hover {
    color: lightblue;
    padding: 18px;
}

.script-entry {
    color: black;
    transition: 0.5s ease-out;
    font-size: 30px;
    position: fixed;
    left: 20px;
    text-decoration: none;
}

.script-entry:hover {
    position: fixed;
    left: 35px;
    letter-spacing: 2px;
    --text-decoration: underline;
    background-color: black;
    border-radius: 10px;
    padding: 5px;
    color: white;
}